<template>
    <div class="qp">
      <div class="qp-1">
        <p>全屏：</p>
        <div>
          &lt;template&gt;<br>
          &lt;div class="quan" @click="handleFullScreen"&gt;<br>
          &lt;el-tooltip effect="dark" :content="fullscreen ? `取消全屏`:`全屏`" placement="bottom"&gt;<br>

          &lt;img src="../../../static/images/icon_4.jpg"/&gt;<br>
          &lt;/el-tooltip&gt;<br>
          &lt;/div&gt;<br>
          &lt;/template&gt;<br><br><br>
          data() {<br>
          return {<br>
          fullscreen: false,<br>
          name:false<br>
          }<br>
          },<br>
          methods: {<br>
          handleFullScreen() {<br>
          let element = document.documentElement;<br>
          if (this.fullscreen) {<br>
          if (document.exitFullscreen) {<br>
          document.exitFullscreen();<br>
          } else if (document.webkitCancelFullScreen) {<br>
          document.webkitCancelFullScreen();
          } else if (document.mozCancelFullScreen) {<br>
          document.mozCancelFullScreen();<br>
          } else if (document.msExitFullscreen) {<br>
          document.msExitFullscreen();<br>
          }<br>
          } else {<br>
          if (element.requestFullscreen) {<br>
          element.requestFullscreen();<br>
          } else if (element.webkitRequestFullScreen) {<br>
          element.webkitRequestFullScreen();<br>
          } else if (element.mozRequestFullScreen) {<br>
          element.mozRequestFullScreen();<br>
          } else if (element.msRequestFullscreen) {<br>
          // IE11<br>
          element.msRequestFullscreen();<br>
          }<br>
          }<br>
          this.fullscreen = !this.fullscreen;<br>
          },<br>
          user(){<br>
          this.name=!this.name;
          }<br>
          }<br>
        </div>
      </div>
      <div class="qp-2">
        <webz-quanp></webz-quanp>
      </div>
    </div>
</template>

<script>
    import WebzQuanp from "../webZuja/WebzQuanp";
    export default {
        name: "WebRightqp",
      components: {WebzQuanp}
    }
</script>

<style lang="scss" scoped>
 .qp{
   width:100%;
   height:100%;
   .qp-1{
     width:50%;
     height:100%;
     float: left;
     padding-left: 15px;
     padding-top: 35px;
     background-color: rgba(135,206,235,.8);
     box-sizing: border-box;
     overflow-x: hidden;
     overflow-y: auto;
     &::-webkit-scrollbar{
       width: 0;
     }
     p{
       font-size: 16px;
       font-weight: 600;
     }
     div{
       font-size: 14px;
       padding: 10px 15px;
     }
   }
   .qp-2{
     width:50%;
     height:100%;
     float: left;
     padding-top: 35px;
     padding-left: 15px;
     box-sizing: border-box;
   }
 }
</style>



