<template>
    <div class="down">
      <div class="down-1">
        <p>下拉菜单：</p>
        <div>
          &lt;template&gt;<br/>
          &lt;div id="assemblySelect" style="padding-left:50px;"&gt;<br/>
          &lt;select name="public-choice" v-model="couponSelected" @change="getCouponSelected" style=" margin:20px auto;width: 30%;height: 50px;font-size: 18px;"&gt;<br/>
          &lt;option value="1" selected disabled style="display:none;color:#ccc;"&gt;--请选择--&lt;/option&gt;<br/>
          &lt;option :value="coupon.id" :disabled="coupon.disabled"  v-for="coupon in couponList"&gt;{ {coupon.name} }&lt;/option&gt;<br/>
          &lt;/select&gt;<br/>
          &lt;/div&gt;<br/>
          &lt;/template&gt;<br/>
          <br>
          data(){<br>
          return {<br>
          disable:false,<br>
          placehoder:[<br>
          {id:"1",name:"---请选择---"}<br>
          ],<br>
          couponList:[<br>
          {id: '2',name: 'one'},<br>
          {id: '3',name: 'two'},<br>
          {id: '4',name: 'three'},<br>
          {id: '5',name: 'four'},<br>
          {id: '6',name: 'five'},<br>
          ],<br>
          couponSelected: '',<br>
          }<br>

          },<br>
          created(){<br>
          //如果没有这句代码，select中初始化会是空白的，默认选中就无法实现<br>
          this.couponSelected = this.placehoder[0].id;<br>
          },<br>
          methods:{<br>
          getCouponSelected(){<br>
          console.log(this.couponSelected)<br>
          },<br>
          }<br>
        </div>
      </div>
      <div class="down-2">
        <webz-down1></webz-down1>
        <webz-down2></webz-down2>
        <webz-down3></webz-down3>
      </div>
    </div>
</template>

<script>
    import WebzDown1 from "../webZuja/WebzDown1";
    import WebzDown2 from "../webZuja/WebzDown2";
    import WebzDown3 from "../webZuja/WebzDown3";
    export default {
        name: "WebRightdown",
      components: {WebzDown3, WebzDown2, WebzDown1}
    }
</script>

<style lang="scss" scoped>
.down{
  width:100%;
  height:100%;
  overflow: hidden;
  .down-1{
    width:50%;
    height:100%;
    float: left;
    padding-left: 15px;
    padding-top: 35px;
    background-color: rgba(135,206,235,.8);
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar{
      width: 0;
    }
    p{
      font-size: 16px;
      font-weight: 600;
    }
    div{
      font-size: 14px;
      padding: 10px 15px;
    }
  }
  .down-2{
    padding-top: 35px;
    width:50%;
    height:100%;
    float: left;
  }
}
</style>



