<template>
 <div class="login">
   <div class="login-1">
      <p>登录：</p>
     <div>
       &lt;template&gt;<br/>
       &lt;div id="Vbee-login"&gt;<br/>
       &lt;!--下面的表单--&gt;<br/>
       &lt;div id="user-form"&gt;<br/>
       &lt;div class="target-title"&gt;<br/>
       &lt;p id="targetCon"&gt;账号名或密码错误，请重试&lt;/p&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!--用户名--&gt;<br/>
       &lt;div class="user-name"&gt;<br/>
       &lt;input type="text" id="username" placeholder="用户名 / 绑定手机 / 绑定邮箱"&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!--密码--&gt;<br/>
       &lt;div class="user-pass"&gt;<br/>
       &lt;!--密码输入框--&gt;<br/>
       &lt;img id="demo_img" src="../../../static/images/eyeclose.png" @click="hideShowPsw()"&gt;<br/>
       &lt;input type="password" id="demo_input" placeholder="请输入密码"/&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!--忘记密码--&gt;<br/>
       &lt;div class="forget-pass"&gt;<br/>
       &lt;a href="#"&gt;忘记密码&lt;/a&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;!--登录按钮--&gt;<br/>
       &lt;div class="user-btn"&gt;<br/>
       &lt;input type="submit" value="登  录" @click="hasinfo"&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;/div&gt;<br/>
       &lt;/template&gt;<br/>

     </div>
   </div>
   <div class="login-2">
     <webz-log></webz-log>
   </div>
 </div>
</template>

<script>
    import WebzLog from "../webZuja/WebzLog";
    export default {
        name: "WebRightLog",
      components: {WebzLog},
    }
</script>

<style lang="scss" scoped>
  .login{
    width:100%;
    height:100%;
    overflow: hidden;
    .login-1{
      width:50%;
      height:100%;
      float: left;
      padding-left: 15px;
      padding-top: 35px;
      background-color: rgba(135,206,235,.8);
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar{
        width: 0;
      }
      p{
        font-size: 16px;
        font-weight: 600;
      }
      div{
        font-size: 14px;
        padding: 10px 15px;
      }
    }
    .login-2{
      width: 50%;
      height:100%;
      float: left;
    }
  }
</style>



