<template>
    <div class="slider">
      <div class="slider-1">
        <p>轮播图：</p>
        <div>
          &lt;template&gt;<br>
          &lt;div class="slide" @mouseover="stop()" @mouseout="move()"&gt;<br>
          &lt;div class="slideshow"&gt;<br>
          &lt;transition-group tag="ul" name="image"&gt;<br>
          &lt;!--创建一个变量与当前遍历的index值比对，若相同则显示，否则隐藏；--&gt;<br>
          &lt;li v-for="(p,index) in img" v-show="index === mark" :key="index"&gt;<br>
          &lt;a href="javascript:0"&gt;<br>
          &lt;img :src="p.pic" /&gt;<br>
          &lt;/a&gt;<br>
          &lt;/li&gt;/<br>
          &lt;/transition-group&gt;<br>
          &lt;/div&gt;<br>
          &lt;div class="bar"&gt;<br>
          &lt;!--按钮index若也与变量相同则当前按钮背景变红。--&gt;<br>
          &lt;span ref="spans" :style="[styles]"  v-for="(s,index) in img" :class="{'active':index===mark}"
          @click="change(index)" :key="index"&gt;&lt;/span&gt;<br>
          &lt;/div&gt;<br>
          &lt;/div&gt;<br>
          &lt;/template&gt;<br>
        </div>
      </div>
      <div class="slider-2">
        <div class="slider-3">
          <webz-slider :img="lis"></webz-slider>
        </div>
      </div>
    </div>
</template>

<script>
    import WebzSlider from "../webZuja/WebzSlider";
    export default {
        name: "WebRightsilder",
      components: {WebzSlider},
      data(){
          return {
            "lis":[
              {"pic":"./static/images/1.jpg"},
              {"pic":"./static/images/2.jpg"},
              {"pic":"./static/images/3.jpg"},
              {"pic":"./static/images/4.jpg"},
              {"pic":"./static/images/skin1.jpg"},
            ]
          }
      }
    }
</script>

<style lang="scss" scoped>
.slider{
  width:100%;
  height:100%;
  .slider-1{
    width:50%;
    height:100%;
    float: left;
    padding-left: 15px;
    padding-top: 35px;
    background-color: rgba(135,206,235,.8);
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar{
      width: 0;
    }
    p{
      font-size: 16px;
      font-weight: 600;
    }
    div{
      font-size: 14px;
      padding: 10px 15px;
    }
  }
  .slider-2{
    width:50%;
    height:100%;
    float: left;
    .slider-3{
    width:80%;
      height:70%;
      margin:0 auto;
    }
  }

}
</style>



