<template>
  <div class="carts">
    <div class="carts-1">
      <p>购物车：</p>
      <div>
        &lt;template&gt;<br/>
        &lt;div class="cart"&gt;<br/>
        &lt;h2&gt;购物车&lt;/h2&gt;<br/>
        &lt;div class="con"&gt;<br/>
        &lt;ul class="uls1"&gt;<br/>
        &lt;li v-for="(p,index) in products"&gt;<br/>
        &lt;div class="imgBox"&gt;<br/>
        &lt;img :src="p.img" alt=""&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;div class="dis"&gt;<br/>
        &lt;div class="part"&gt;<br/>
        &lt;div class="left"&gt;<br/>
        &lt;h4 class="name"&gt;{&nbsp;{p.name}&nbsp;}&lt;/h4&gt;<br/>
        &lt;p&gt;{&nbsp;{p.type}&nbsp;}&lt;/p&gt;<br/>
        &lt;p&gt;{&nbsp;{p.contain}&nbsp;}&lt;/p&gt;<br/>
        &lt;div class="count"&gt;<br/>
        &lt;span @click="minQal(index)"&gt;-&lt;/span&gt;<br/>
        &lt;input type="text" v-model="p.num"&gt;<br/>
        &lt;span @click="addQal(index)"&gt;+&lt;/span&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;div class="right"&gt;<br/>
        &lt;div class="price"&gt;￥{ {p.new} }&lt;/div&gt;<br/>
        &lt;div class="old"&gt;￥{ {p.old} }&lt;/div&gt;<br/>
        &lt;i class="iconfont icon-cuohao"&gt;&lt;/i&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/li&gt;<br/>
        &lt;/ul&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;div class="footer"&gt;<br/>
        &lt;p&gt;总金额 &lt;span&gt;￥{ {totalMoney} }&lt;/span&gt;&lt;/p&gt;<br/>
        &lt;router-link to="#"&gt;结算&lt;/router-link&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/template&gt;<br/>

        <br/>
        data(){<br/>
        return{<br/>
        totalNum:0,<br/>
        totalMoney:0,<br/>
        products: this.produc<br/>
        }<br/>
        },<br/>
        created(){<br/>
        this._countCarts(this.products)<br/>
        },<br/>
        methods:{<br/>
        // 统计出总价格和总数量<br/>
        _countCarts(products) {<br/>
        this.totalNum = 0<br/>
        this.totalMoney = 0<br/>
        products.forEach((item,index)=>{<br/>
        this.totalNum += parseFloat(item.num)<br/>
        this.totalMoney += item.num * item.new<br/>
        })<br/>
        },<br/>
        // 点击增加件数<br/>
        addQal(i) {<br/>
        this.products[i].num++<br/>
        },<br/>
        // 点击减少件数<br/>
        minQal(i) {<br/>
        if( this.products[i].num <=1){<br/>
        this.products[i].num =1<br/>
        }else{<br/>
        this.products[i].num--<br/>
        }<br/>
        },<br/>
        },<br/>
        watch:{<br/>
        //监听product中的数量发生变化，重新计算<br/>
        products:{<br/>
        handler:function (n,o) {<br/>
        this._countCarts(this.products)<br/>
        },<br/>
        deep:true<br/>
        },<br/>
        }<br/>
      </div>
    </div>
    <div class="carts-2">
        <div class="carts-3">
          <img src="../../../static/images/iphone2.png" >
          <div class="carts-4">
            <webz-cat :produc="product"></webz-cat>
          </div>
       </div>
    </div>
  </div>
</template>

<script>
    import WebzCat from "../webZuja/WebzCat";
    export default {
        name: "WebRightCat",
         data(){
        return{
          product:[
            {
              "img":"./static/images/g1.jpg",
              "name":"【超值入门体验】蒂佳婷Dr.Jart+7天面膜套装 韩国美白补水保湿药丸面膜",
              "type":"明星同款护肤肌密",
              "contain":"均码",
              "num":"1",
              "new":"129",
              "old":"280"
            },
            {
              "img":"./static/images/g2.jpg",
              "name":"唯品自营 | 【改造干皮星人】水润鲜活面膜礼盒 18片 补水细腻面膜套装",
              "type":"一叶子One Leaf护肤品专场",
              "contain":"18片",
              "num":"1",
              "new":"109",
              "old":"285"
            }
          ]
        }
      },
      components: {WebzCat},
    }
</script>

<style lang="scss" scoped>
 .carts{
   width:100%;
   height:100%;
   .carts-1{
     width:50%;
     height:100%;
     float: left;
     padding-left: 15px;
     padding-top: 35px;
     background-color: rgba(135,206,235,.8);
     box-sizing: border-box;
     overflow-x: hidden;
     overflow-y: auto;
     &::-webkit-scrollbar{
       width: 0;
     }
     p{
       font-size: 16px;
       font-weight: 600;
     }
     div{
       font-size: 14px;
       padding: 10px 15px;
     }
   }
   .carts-2{
     width:50%;
     height:100%;
     float: left;
     padding-top: 35px;
     .carts-3{
       width:50%;
       height:90%;
       margin:0 auto;
       position: relative;
       img{
         width:100%;
         height:100%;
       }
       .carts-4{
         width:242px;
         height:403px;
         position: absolute;
         left:25px;
         top:60px;
         background:#fff;
         overflow: hidden;
       }
     }
   }
 }
</style>



