<template>
  <div class="ue">
    <div class="ue-1">
      <p>点击刷新：</p>
      <div>
        &lt;div id="app"&gt;<br/>
        &lt;loading duration='2s' :isshow='show'&gt;&lt;/loading&gt;<br/>
        &lt;button @click="show = !show"点击刷新&gt;&lt;/button&gt;<br/>
        &lt;/div&gt;<br/><br><br>
        import LoadingComponent from './WebzUe.vue'<br>
        let Loading = {};<br>
        Loading.install = (Vue) => {<br>
        Vue.component('loading', LoadingComponent)<br>
        }<br>
        export default Loading;<br>
      </div>
    </div>
    <div class="ue-2">
      <div id="app">
        <loading duration='2s' :isshow='show'></loading>
        <button @click="show = !show">点击刷新</button>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "WebRightue",
        data() {
          return {
            show: false
          };
        },
    }
</script>

<style lang="scss" scoped>
.ue{
  width:100%;
  height:100%;
  .ue-1{
    width:50%;
    height:100%;
    float: left;
    padding-left: 15px;
    padding-top: 35px;
    background-color: rgba(135,206,235,.8);
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar{
      width: 0;
    }
    p{
      font-size: 16px;
      font-weight: 600;
    }
    div{
      font-size: 14px;
      padding: 10px 15px;
    }
  }
  .ue-2{
    width:50%;
    height:100%;
    float: left;
    padding-top: 35px;
    padding-left: 15px;
    box-sizing: border-box;
  }
}
</style>



