<template>
<div id="index">

  <canvas id="demo-canvas"></canvas>
  <div class="con">
    <img src="../../static/images/logo1.png" alt="" class="logo">
    <p>一套基于Vue.js的免费开源组件库</p>
    <div class="item">
      <router-link to="/example">示例</router-link>
      <router-link to="/extend">扩展</router-link>
      <router-link to="/template">模板</router-link>
      <router-link to="/team">团队介绍</router-link>
      <a href="https://github.com/Vbee-team/www.vbee"><i class="iconfont icon-GitHub"></i>GitHub</a>
    </div>
  </div>
</div>
</template>

<script>
    export default {
      name: "WebIndex",
      mounted(){
        (function() {

          var width, box, height, largeHeader, canvas, ctx, circles, target, animateHeader = true;

          // Main
          initHeader();
          addListeners();

          function initHeader() {
            width = window.innerWidth;
            height = window.innerHeight;
            // height = 300;

            canvas = document.getElementById('demo-canvas');
            canvas.width = width;
            canvas.height = height;
            ctx = canvas.getContext('2d');

            // create particles
            circles = [];
            for(var x = 0; x < width*0.5; x++) {
              var c = new Circle();
              circles.push(c);
            }
            animate();
          }

          // Event handling
          function addListeners() {
            window.addEventListener('scroll', scrollCheck);
            window.addEventListener('resize', resize);
          }

          function scrollCheck() {
            if(document.body.scrollTop > height) animateHeader = false;
            else animateHeader = true;
          }

          function resize() {
            canvas.width = width;
            canvas.height = height;
          }

          function animate() {
            if(animateHeader) {
              ctx.clearRect(0,0,width,height);
              for(var i in circles) {
                circles[i].draw();
              }
            }
            requestAnimationFrame(animate);
          }

          // Canvas manipulation
          function Circle() {
            var _this = this;

            // constructor
            (function() {
              _this.pos = {};
              init();
            })();

            function init() {
              _this.pos.x = Math.random()*width;
              _this.pos.y = height+Math.random()*100;
              _this.alpha = 0.1+Math.random()*0.3;
              _this.scale = 0.1+Math.random()*0.3;
              _this.velocity = Math.random();
            }

            this.draw = function() {
              if(_this.alpha <= 0) {
                init();
              }
              _this.pos.y -= _this.velocity;
              _this.alpha -= 0.0005;
              ctx.beginPath();
              ctx.arc(_this.pos.x, _this.pos.y, _this.scale*10, 0, 2 * Math.PI, false);
              ctx.fillStyle = 'rgba(255,255,255,'+ _this.alpha+')';
              ctx.fill();
            };
          }

        })();
      }
    }
</script>

<style lang="scss" scoped>
  #index{
    height:100%;
    text-align: center;
    font-size: 16px;
    background: url("../../static/images/skin-blue.jpg") no-repeat center bottom;
    background-size: cover;
    position: relative;
    #demo-canvas{
      position: absolute;
      bottom: 0;
      left: 0;
      z-index: 0;
    }
    .con{
      position: absolute;
      top: 0;
      left: 36%;
      .logo{
        width: 90px;
        height: 117px;
        margin-top: 65px;
      }
      p{
        margin: 50px 0;
        color: #fff;
      }
      .item{
        margin:0 auto;
        a{
          font-size: 16px;
          color: #01a8fb;
          display: inline-block;
          padding: 5px 8px;
          transition: all 1s;
          box-sizing: border-box;
          margin: 0 5px;
          border-top: 2px solid transparent;
          border-bottom: 2px solid transparent;
          &:hover{
            border-top: 2px solid #fff;
            border-bottom: 2px solid #fff;
            color: #fff;
          }
        }
      }
    }

  }
</style>



