<template>
  <div id="templateSearch">
    <input type="text" placeholder="搜索你所需要的模板应用"/>
    <button class="templateBtn">
      <i class="iconfont icon-search"></i>
    </button>
    <div class="hotSearch">
      <span>热搜 : </span>
      <a href="#">后台</a>
      <a href="#">商城</a>
      <a href="#">博客</a>
      <a href="#">社区</a>
    </div>
  </div>
</template>

<script>
  export default {
    name: "TemplateSearch"
  }
</script>

<style scoped lang="scss">
  #templateSearch{
    height: 50px;
    width: 800px;
    margin:50px auto 0;
    input{
      width: 500px;
      height: 50px;
      border:none;
      outline: none;
      float: left;
      text-indent: 10px;
      vertical-align: middle;
      display: block;
      background: #eff4f5;
    }
    .templateBtn{
      display: block;
      width: 100px;
      height: 50px;
      float: left;
      background: #16c9e9;
      border:none;
      vertical-align: middle;
      .iconfont{
        font-size:24px;
        color:#fff;
      }
    }
    /*热搜*/
    .hotSearch{
      width: 100px;
      height: 20px;
      font-size: 12px;
      color:#9495a4;
      float: left;
      white-space: nowrap;
      padding-top:30px;
      padding-left:14px;
      border-radius: 10px;
      span{
        font-weight: 600;
      }
      a{
        color:#ccc;
      }
    }
  }
</style>



