<template>
    <div class="slide" @mouseover="stop()" @mouseout="move()">
      <div class="slideshow">
          <transition-group tag="ul" name="image">
            <!--创建一个变量与当前遍历的index值比对，若相同则显示，否则隐藏；-->
            <li v-for="(p,index) in img" v-show="index === mark" :key="index">
              <a href="javascript:0">
                <img :src="p.pic" />
              </a>
            </li>/
          </transition-group>
      </div>
      <div class="bar">
        <!--按钮index若也与变量相同则当前按钮背景变红。-->
        <span ref="spans" :style="[styles]"  v-for="(s,index) in img" :class="{'active':index===mark}" @click="change(index)" :key="index"></span>
      </div>
    </div>
</template>

<script>
    export default {
        name: "WebzSlider",
        props:["img","styles"],
        data(){
          return {
            timer:null,
            mark:0,
          }
        },
      mounted(){
      },
      methods:{
          autoplay(){
            this.mark++;
            if(this.mark=== this.img.length){ //循环到最后一张图片归零
              this.mark = 0
            }
          },
        play(){
            this.timer=setInterval(this.autoplay,2500)
        },
        change(i){
            this.mark=i
        },
        stop(){
            clearInterval(this.timer)
        },
        move(){
          this.timer=setInterval(this.autoplay,2500)
        }
      },
      created(){
          this.play()
      }
    }
</script>

<style lang="scss" scoped>
.slide{
  width:100%;
  height:100%;
  overflow: hidden;
  position: relative;
  .slideshow{
    width:100%;
    height:100%;
    ul{
      width:100%;
      height:100%;
      li{
        width:100%;
        height:100%;
        position: absolute;
        img{
          width:100%;
          height:100%;
        }
      }
    }
  }
  .bar{
    width:100%;
    position: absolute;
    bottom:10px;
    z-index: 10;
    text-align: center;
    span{
    display: inline-block;
      width:10px;
      height:10px;
      border:1px solid #ccc;
      background:#fff;
      margin-right:10px;
      border-radius:50%;
      cursor: pointer;
    }
    .active{
      background:red !important;
    }
  }
}
</style>
<style scoped>
  .image-enter-active{
    transform: translateX(0);
    transition: all 0.5s ease;
  }
  .image-leave-active{
    transform: translateX(-100%);
    transition: all 0.5s ease;
  }
  .image-enter{
    transform: translateX(100%);
  }
  .image-leave{
    transform: translateX(0);
  }
</style>



