<template>
  <div class="cart">
    <h2>购物车</h2>
    <div class="con">
      <ul class="uls1">
        <li v-for="(p,index) in products">
          <div class="imgBox">
            <img :src="p.img" alt="">
          </div>
          <div class="dis">
            <div class="part">
              <div class="left">
                <h4 class="name">{{p.name}}</h4>
                <p>{{p.type}}</p>
                <p>{{p.contain}}</p>
                <div class="count">
                  <span @click="minQal(index)">-</span>
                  <input type="text" v-model="p.num">
                  <span @click="addQal(index)">+</span>
                </div>
              </div>
              <div class="right">
                <div class="price">￥{{p.new}}</div>
                <div class="old">￥{{p.old}}</div>
                <i class="iconfont icon-cuohao"></i>
              </div>
            </div>
          </div>
        </li>
      </ul>
    </div>
    <div class="footer">
      <p>总金额 <span>￥{{totalMoney}}</span></p>
      <router-link to="#">结算</router-link>
    </div>
  </div>
</template>

<script>
    export default {
      props:["produc"],
      name: "WebzCat",
      data(){
        return{
          totalNum:0,
          totalMoney:0,
          products: this.produc
        }
      },
      created(){
        this._countCarts(this.products)
      },
      methods:{
        // 统计出总价格和总数量
        _countCarts(products) {
          this.totalNum = 0
          this.totalMoney = 0
          products.forEach((item,index)=>{
            this.totalNum += parseFloat(item.num)
            this.totalMoney += item.num * item.new
          })
        },
        // 点击增加件数
        addQal(i) {
          this.products[i].num++
        },
        // 点击减少件数
        minQal(i) {
          if( this.products[i].num <=1){
            this.products[i].num =1
          }else{
            this.products[i].num--
          }
        },
      },
      watch:{
        //监听product中的数量发生变化，重新计算
        products:{
          handler:function (n,o) {
            this._countCarts(this.products)
          },
          deep:true
        },
      }
    }
</script>

<style lang="scss" scoped>
  .cart{
    height: 100%;
    display: flex;
    flex-direction: column;
    background-color: #f3f4f6;
    h2{
      text-align:center;
    }
    .con{
      height: 100%;
      flex: 1;
      overflow-x: hidden;
      overflow-y: auto;
      .uls1{
        margin-top: 10px;
        li{
          padding:6px 8px;
          display: flex;
          .imgBox{
            width: 58px;
            height: 78px;
            margin-right: 8px;
            img{
              width: 100%;
              height: 100%;
            }
          }
          .dis{
            flex: 1;
            .part{
              display: flex;
              .left{
                .name{
                  display: -webkit-box;
                  font-weight: 400;
                  overflow: hidden;
                  text-overflow: ellipsis;
                  -webkit-box-orient: vertical;
                  line-height: 14px;
                  -webkit-line-clamp: 2;
                }
                p{
                  font-size: 12px;
                  line-height: 14px;
                  color: #999;
                }
                .count{
                  display: flex;
                  color: #848891;
                  input{
                    width: 30px;
                    height: 18px;
                    display: inline-block;
                    border: 1px solid #e7e7e7;
                    text-align: center;
                    background-color: transparent;
                  }
                  span{
                    text-align: center;
                    display: inline-block;
                    width: 20px;
                    height: 18px;
                    border: 1px solid #e7e7e7;
                    cursor: pointer;
                  }
                }
              }
              .right{
                text-align: right;
                margin-left: 4px;
                .price{
                  color: #222;
                }
                .old{
                  font-size: .1rem;
                  color: #98989f;
                  text-decoration: line-through;
                  margin-bottom: .44rem;
                }
              }
            }
          }
        }
      }
    }
    .footer{
      padding: 0 6px;
      font-size: 12px;
      text-align: center;
      background-color: #fff;
      p{
        font-size: 14px;
        line-height: 25px;
        span{
          color: #dd0083;
        }
      }
      a{
        display: inline-block;
        width: 100%;
        line-height: 25px;
        background-color: #dd0083;
        color: #fff;
        margin-bottom: 4px;
      }
    }
  }
</style>



