<template>
  <div id="assemblySelect" style="padding-left:50px;">
    <h2>下拉菜单选择框</h2>
    <!--基础语法-->
    <select name="public-choice" v-model="couponSelected" @change="getCouponSelected" style=" margin:20px auto;width: 30%;height: 50px;font-size: 18px;">
      <option value="1" selected disabled style="display:none;color:#ccc;">--请选择--</option>
      <option :value="coupon.id" v-for="coupon in couponList">{{coupon.name}}</option>
    </select>
  </div>
</template>

<script>
    export default {
        name: "WebzDown1",
      data(){
        return {
          disable:false,
          placehoder:[
            {id:"1",name:"---请选择---"}
          ],
          couponList:[
            {id: '2',name: 'one'},
            {id: '3',name: 'two'},
            {id: '4',name: 'three'},
            {id: '5',name: 'four'},
            {id: '6',name: 'five'},
          ],
          couponSelected: '',
        }

      },
      created(){
        //如果没有这句代码，select中初始化会是空白的，默认选中就无法实现
        this.couponSelected = this.placehoder[0].id;

      },
      methods:{
        getCouponSelected(){
          console.log(this.couponSelected)
        },
      }
    }
</script>

<style scoped>

</style>



