<template>
    <div class="slid">
      <div class="slid-1">
        <p>标准性进度条：</p>
        <div>
          &lt;template&gt;<br>
          &lt;div class="slider" ref="slider"&gt;<br>
          &lt;div ref="pro" class="process" :style="{width}"&gt; &lt;/div&gt;<br>
          &lt;div class="thunk" ref="thunk" :style="{left}"&gt;<br>
          &lt;div @mousedown="show=true" @mouseup="show=false" @mouseleave="show=false" class="block"&gt;&lt;/div&gt;<br>
          &lt;div v-show="show" class="tips"&gt;<br>
          &lt;span&gt;{{scale*100}}&lt;/span&gt;<br>
          &lt;i class="fas fa-caret-down"&gt;&lt;/i&gt;<br>
          &lt;/div&gt;<br>
          &lt;/div&gt;<br>
          &lt;/div&gt;<br>
          &lt;/template&gt;<br>
        </div>
        <p>迷你型进度条：</p>
        <div>
          &lt;template&gt;<br>
          &lt;div class="slider" ref="slider"&gt;<br>
          &lt;div ref="pro" class="process" :style="{width}"&gt; &lt;/div&gt;<br>
          &lt;div class="thunk" ref="thunk" :style="{left}"&gt;<br>
          &lt;div @mousedown="show=true" @mouseup="show=false" @mouseleave="show=false" class="block"&gt;&lt;/div&gt;<br>
          &lt;div v-show="show" class="tips"&gt;<br>
          &lt;span&gt;{{scale*100}}&lt;/span&gt;<br>
          &lt;i class="fas fa-caret-down"&gt;&lt;/i&gt;<br>
          &lt;/div&gt;<br>
          &lt;/div&gt;<br>
          &lt;/div&gt;<br>
          &lt;/template&gt;<br>
        </div>
        <p>迷你进度显示进度条：</p>
        <div>
          &lt;template&gt;<br/>
          &lt;div class="slider" ref="slider"&gt;<br/>
          &lt;div ref="pro" class="process" :style="{width}"&gt; &lt;/div&gt;<br/>
          &lt;div class="thunk" ref="thunk" :style="{left}"&gt;<br/>
          &lt;div class="block"&gt;&lt;/div&gt;<br/>
          &lt;div class="tips"&gt;<br/>
          &lt;span&gt;{{scale*100}}&lt;/span&gt;<br/>
          &lt;i class="fas fa-caret-down"&gt;&lt;/i&gt;<br/>
          &lt;/div&gt;<br/>
          &lt;/div&gt;<br/>
          &lt;/div&gt;<br/>
          &lt;/template&gt;<br/>
        </div>
      </div>
      <div class="slid-2">
        <p>标准性进度条：</p>
        <webz-sild :min=0 :max=100 value="20"></webz-sild>
        <p>迷你型进度条：</p>
        <webz-slid1 :min=0 :max=100 value="10"></webz-slid1>
        <p>迷你进度显示进度条：</p>
        <webz-slid2 :min=0 :max=100 value="10"></webz-slid2>
      </div>
    </div>
</template>

<script>
    import WebzSild from "../webZuja/WebzSild";
    import WebzSlid1 from "../webZuja/WebzSlid1";
    import WebzSlid2 from "../webZuja/WebzSlid2";
    export default {
        name: "WebRightslid",
      components: {WebzSlid2, WebzSlid1, WebzSild}
    }
</script>

<style lang="scss" scoped>
.slid{
  width:100%;
  height:100%;
  .slid-1{
    width:50%;
    height:100%;
    float: left;
    padding-left: 15px;
    padding-top: 35px;
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    background-color: rgba(135,206,235,.6);
    &::-webkit-scrollbar{
      width: 0;
    }
    p{
      font-size: 16px;
      font-weight: 600;
    }
    div{
      font-size: 14px;
      padding: 10px 15px;
    }
  }
  .slid-2{
    width:50%;
    height:100%;
    float: left;
    padding: 35px 15px 15px 15px;
    box-sizing: border-box;
  }
}
</style>



