<template>
  <div id="app1">
    <a href="javascript:0" @click="showChose=!showChose">点击显示遮罩层</a>
    <transition name="mybox">
      <div class="zhezh" v-show="showChose">
      </div>
    </transition>
    <transition name="fade">
      <div class="hides" v-show="showChose">
        <a @click="showChose=false" href="javascript:0">关闭遮罩层</a>
      </div>
    </transition>
  </div>
</template>

<script>
    export default {
        name: "WebzMask",
      data(){
        return  {
          showChose:false
        }
      }
    }
</script>

<style lang="scss" scoped>
  #app1{
    width:100%;
    height:100%;
    position: relative;
    a{
      font-size:.28rem;
      color:black;
    }
  }
  .zhezh{
    width:100%;
    height:100%;
    background:rgba(0,0,0,.6);
    position: absolute;
    top: 0;
    left: 0;
  }
  .hides{
    width:100%;
    height:67%;
    position:absolute;
    bottom: 0;
    left:0;
    background:#fff;}
</style>
<style>
  .fade-enter-active, .fade-leave-active {
    transition: all 1s;
    margin-bottom: 0rem;
  }
  .fade-enter, .fade-leave-active {
    margin-bottom: -6rem;
  }
  .mybox-enter-active,.mybox-leave-active{
    transition: all 1s;
    opacity: 1;
  }
  .mybox-enter,.mybox-leave-active{
    opacity: 0;
  }
</style>



