<template>
  <div id="app1">
    <div class="panel panel-primary">
      <div class="panel-heading">用户管理</div>
      <table class="table table-bordered table-striped text-center">
        <thead>
        <tr>
          <th>序号</th>
          <th>用户名</th>
          <th>年龄</th>
          <th>毕业学校</th>
          <th>操作</th>
        </tr>
        </thead>
        <tbody>
        <tr v-for ="(user,index) in users">
          <td>{{index+1}}</td>
          <td>{{user.name}}</td>
          <td>{{user.age}}</td>
          <td>{{user.school}}</td>
          <td><button v-on:click="remove(index)">remove</button></td>
        </tr>
        <tr>
          <td></td>
          <td><input type="text"  id="name" v-model="user.name"/></td>
          <td><input type="text" id="age" v-model="user.age"/></td>
          <td><input type="text" id="school" v-model="user.school"/></td>
          <td><button @click="insert">insert</button></td>
        </tr>
        </tbody>
      </table>
    </div>
  </div>
</template>

<script>
    export default {
        name: "WebzFrom",
      data () {
        return {
          user: {'name': '', 'age': '', 'school': ''},
          users: [
            {'name': '李磊', 'age': '25', 'school': '洛阳理工'},
            {'name': '张成', 'age': '23', 'school': '桂林电子科技'},
            {'name': '炼心', 'age': '22', 'school': '江西电子科技'}
          ]
        }
      },
      methods: {
        insert: function () {
          this.users.push(this.user)
        },
        remove: function (index) {
          this.users.splice(index, 1)
        }
      }
    }
</script>

<style lang="scss" scoped>
  #app1{
    width: 100%;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
  .panel-heading{
    font-size: 16px;
    text-align: center;
    font-weight: 600;
  }
  tr,th{
    text-align:center;
  }
  input{
    width: 40%;
  }
  }

</style>



