<template>
    <div class="citys">
      <div class="citys-1">
        <p>地址选择：</p>
        <div>
          methods: {<br>
          choseAdd: function() {<br>
          this.showChose = true;<br>
          },<br>
          closeAdd: function() {<br>
          this.showChose = false;<br>
          },<br>
          _filter(add,name,code) {<br>
          let result = [];<br>
          for(let i=0;i<add.length;i++) {<br>
          if(code == add[i].id){<br>
          result = add[i][name];<br>
          }<br>
          }<br>
          return result;<br>
          },<br>
          getProvinceId: function(code,input,index) {<br>
          this.province = code;<br>
          this.Province = input;<br>
          this.showProvince=false;<br>
          this.showCity=true;<br>
          this.showDistrict = false;<br>
          this.showCityList = this._filter(this.info,'city',this.province);<br>
          // 点击选择当前<br>
          this.info.map( a => a.selected = false );<br>
          this.info[index].selected = true;<br>
          this.$refs.selectUl.scrollTop = 0;<br>
          },<br>
          provinceSelected: function() {<br>
          // 清除市级和区级列表<br>
          this.showCityList = false;<br>
          this.showDistrictList = false;<br>
          // 清除市级和区级选项<br>
          this.City = false;<br>
          this.District = false;<br>
          // 选项页面的切换<br>
          this.showProvince = true;<br>
          this.showCity = false;<br>
          this.showDistrict = false;<br>
          },<br>
          getCityId: function(code, input, index) {<br>
          this.city = code;<br>
          this.City = input;<br>
          this.showProvince=false;<br>
          this.showCity=false;<br>
          this.showDistrict = true;<br>
          this.showDistrictList = this._filter(this.showCityList,'district',this.city);<br>
          // 选择当前添加active<br>
          this.showCityList.map( a => a.selected = false );<br>
          this.showCityList[index].selected = true;<br>
          this.$refs.selectUl.scrollTop = 0;<br>
          },<br>
          citySelected: function() {<br>
          this.showProvince=false;<br>
          this.showCity=true;<br>
          this.showDistrict = false;<br>
          },<br>
          getDistrictId: function(code, input, index) {<br>
          this.district = code;<br>
          this.District = input;<br>
          // 选择当前添加active<br>
          this.showDistrictList.map( a => a.selected = false );<br>
          this.showDistrictList[index].selected = true;<br>
          this.area = this.Province + this.City + this.District;<br>
          // 选取市区选项之后关闭弹层<br>
          this.showChose = false;<br>
          this.sendValue(this.area);<br>
          this.$refs.selectUl.scrollTop = 0;<br>
          },<br>
          districtSelected: function() {<br>
          this.showProvince=false;<br>
          this.showCity=false;<br>
          this.showDistrict = true;<br>
          },<br>
          //给父组件传值<br>
          sendValue(val) {<br>
          this.$emit('getValue',val);<br>
          }<br>
          }<br>
        </div>
      </div>
      <div class="citys-2">
        <div class="citys-3">
          <img src="../../../static/images/iphone2.png" >
          <div class="citys-4">
            <webz-ssld></webz-ssld>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
    import WebzSsld from "../webZuja/WebzSsld";
    export default {
        name: "WebRightssld",
      components: {WebzSsld},
    }
</script>

<style lang="scss" scoped>
.citys{
  width:100%;
  height:100%;
  overflow: hidden;
  .citys-1{
    width:50%;
    height:100%;
    float: left;
    padding-left: 15px;
    padding-top: 35px;
    background-color: rgba(135,206,235,.8);
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar{
      width: 0;
    }
    p{
      font-size: 16px;
      font-weight: 600;
    }
    div{
      font-size: 14px;
      padding: 10px 15px;
    }
  }
  .citys-2{
    width:50%;
    height:100%;
    float: left;
    padding-top: 35px;
      .citys-3{
        width:50%;
        height:90%;
        margin:0 auto;
        position: relative;
        img{
          width:100%;
          height:100%;
        }
        .citys-4{
          width:241px;
          height:380px;
          position: absolute;
          left:25px;
          top:60px;
          background:#fff;
          overflow: hidden;
          margin-top: -4px;

        }
      }
  }
}
</style>



