<template>
  <div id="Vbee-login">
    <!--下面的表单-->
    <div id="user-form">
      <div class="target-title">
        <p id="targetCon">账号名或密码错误，请重试</p>
      </div>
      <!--用户名-->
      <div class="user-name">
        <input type="text" id="username" placeholder="用户名 / 绑定手机 / 绑定邮箱">
      </div>
      <!--密码-->
      <div class="user-pass">
        <!--密码输入框-->
        <img id="demo_img" src="../../../static/images/eyeclose.png" @click="hideShowPsw()">
        <input type="password" id="demo_input" placeholder="请输入密码"/>
      </div>
      <!--忘记密码-->
      <div class="forget-pass">
        <a href="#">忘记密码</a>
      </div>
      <!--登录按钮-->
      <div class="user-btn">
        <input type="submit" value="登  录" @click="hasinfo">
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "WebzLog",
      methods:{
        hideShowPsw:function () {
          // 这里使用最原始的js语法实现，可对应换成jquery语法进行逻辑控制
          var demoImg = document.getElementById("demo_img")
          var demoInput = document.getElementById("demo_input")

          //隐藏text block，显示password block
          if (demoInput.type == "password") {
            demoInput.type = "text";
            demoImg.src = "./static/images/eyeopen.png";
          }else {
            demoInput.type = "password";
            demoImg.src = "./static/images/eyeclose.png";
          }
        },

        // 点击登录按钮判断input框里是否有内容
        hasinfo:function(){
          var demoName = document.getElementById("username")
          var demoInput = document.getElementById("demo_input")
          var demoText = document.getElementById("targetCon")
          console.log(demoName.value)
          console.log(demoInput.value)
          if(demoName.value =="" || demoInput.value == ""){
            demoText.style.display = "block"
            demoText.innerHTML ="用户名和密码不能为空";
          }else if(demoName.onblur || demoInput.onblur){
            demoText.style.display = "none"
            demoText.innerHTML = ""
          }else{
            demoText.style.display = "none"
            demoText.innerHTML = ""
          }

        },
        //当input再次聚焦时 提示框取消

      }
    }
</script>

<style scoped>
  #Vbee-login{
    padding:0 30px;
  }
  #user-header img{
    width: 80px;
    height:80px;
    display: block;
    margin:0 auto;
    border-radius:50%;
  }
  #user-form{
    padding:30px;
  }
  /*用户名及密码错误提示框*/
  #user-form .target-title p{
    font-size: .1rem;
    color:#e32b1f;
    display: none;
  }
  /*用户名*/
  #user-form .user-name{
    border-bottom: 2px solid #d3d3d8;
  }
  /*用户密码*/
  #user-form .user-pass{
    border-bottom: 2px solid #d3d3d8;
    position: relative;
  }
  /*密码里的眼睛*/
  #demo_img{
    width: 20px;
    height: 15px;
    display: block;
    position: absolute;
    right: 24px;
    top:20px;
  }
  /*form表单里的input样式*/
  #user-form input{
    width: 100%;
    height: 50px;
    font-size: 14px;
    color:#c3c2c3;
    border:none;
    outline: none;
    line-height:50px;
    display: flex;
    text-indent: 10px;
  }
  /*忘记密码*/
  .forget-pass{
    display: flex;
    justify-content:flex-end;
    height: 40px;
    align-items: center;
    padding-bottom: 10px;
  }
  .forget-pass a{
    font-size: .1rem;
    color:#87888d;
  }
  /*登录按钮.*/
  .user-btn input{
    height: .4rem;
    background: #16c9e9;
    color:#f4add7;
    font-size: .12rem;
    display: flex;
    justify-content: center;
  }
</style>



