      <!--侧边栏-->
      <div class="sec-left">
        <ul>
          <li><a href="#" @click="toogleTabs(one)">进度条</a></li>
          <li><a href="#" @click="toogleTabs(two)">遮罩层</a></li>
          <li><a href="#" @click="toogleTabs(three)">轮播图</a></li>
          <li><a href="#" @click="toogleTabs(four)">地区选择</a></li>
          <li><a href="#" @click="toogleTabs(five)">弹出框</a></li>
          <li><a href="#" @click="toogleTabs(six)">全屏</a></li>
          <li><a href="#" @click="toogleTabs(seven)">日历</a></li>
          <li><a href="#" @click="toogleTabs(eight)">省市联动（pc）</a></li>
          <li><a href="#" @click="toogleTabs(nine)">购物车</a></li>
          <li><a href="#" @click="toogleTabs(ten)">下拉菜单</a></li>
          <li><a href="#" @click="toogleTabs(eleven)">点击刷新</a></li>
