      <!--右边的内容-->
      <div class="examplesectionCon">
        <one :is="computView" keep-alive  :products="products"></one>
      </div>
    </div>
</template>

<script>
  import one from "../websectionright/WebRightslid"
  import two from "../websectionright/WebRightmask"
  import three from "../websectionright/WebRightsilder"
  import four from "../websectionright/WebRightssld"
  import five from "../websectionright/Webrighttanchu";
  import ten from "../websectionright/WebRightdown"
  import six from "../websectionright/WebRightqp"
  import seven from "../websectionright/WebRightNews";
  import nine from "../websectionright/WebRightCat";
  import twelve from "../websectionright/WebRightFrom"
  import eight from "../websectionright/WebRightsanj"
  import eleven from "../websectionright/WebRightue"
  import thirteen from "../websectionright/WebRightLog";

    export default {
        name: "ExampleSection",
        data(){
          return {
            one:'one',
            two:'two',
            three:'three',
            four:'four',
            five:'five',
            seven:'seven',
            twelve:"twelve",
            nine:'nine',
            six:"six",
            ten:"ten",
            eight:"eight",
            eleven:"eleven",
            thirteen:'thirteen',
            computView:'one',
          }
        },
      components:{
        thirteen,
        nine,
        seven,
        one,
        eight,
        two,
        twelve,
        six,
        eleven,
        three,
        four,
        ten,
        five,
      },
      methods:{
        toogleTabs (tabText){
          this.computView = tabText
        }
      }
    }
</script>

<style lang="scss" scoped>
  #examplesection{
    width:100%;
    height:89.5%;
    overflow: hidden;
    .sec-left{
      width:15%;
      height:100%;
      float:left;
      ul{
        background-color: rgba(135,206,235,.8);
        li{
          width: 85%;
          text-align: center;
          line-height: 45px;
          border-bottom: 1px solid #b2b2b2;
          margin: 0 auto;
          a{
            color: dimgray;
          }

        }
      }
    }
    .examplesectionCon{
      width:85%;
      height:100%;
      float:left;
      box-sizing: border-box;
      background-color: rgba(135,206,235,.8);
    }
  }
</style>



