<template>
    <div class="extend">
      <public-header></public-header>
      <div class="con">
        <div class="title">
          <p>VbeeUI 第三方组件平台</p>
        </div>
        <div class="search">
          <input type="text" placeholder="搜索 VbeeUI 第三方组件库">
          <button><i class="iconfont icon-search"></i></button>
        </div>
        <a>发布组件</a>
        <div class="next">期待您的加入...</div>
      </div>
    </div>
</template>

<script>
    import PublicHeader from "../components/PublicHeader";
    export default {
        name: "WebExtend",
      components: {PublicHeader}
    }
</script>

<style lang="scss" scoped>
  .extend{
    height: 100%;
    .con{
      height: 89.8%;
      background: url("../../static/images/skin-ocean.jpg") no-repeat;
      background-size: cover;
      .title{
        width: 16%;
        margin: 0 auto;
        padding-top: 75px;
        text-align: center;
        p{
          width: auto;
          display: inline-block;
          font-size: 20px;
          color: #fff;
        }
      }
      .search{
        width: 50%;
        height: 42px;
        background: red;
        display: flex;
        margin: 70px auto;
        input{
          flex: 1;
          text-indent: 15px;
          border: 0;
          outline: 0;
        }
        button{
          width: 60px;
          line-height: 38px;
          background-color: #009688;
          border: 0;
          .iconfont{
            font-size: 20px;
            color: #fff;
          }
        }
      }
      a{
        width: 134px;
        line-height: 42px;
        display: block;
        font-size: 18px;
        color: #fff;
        border: 1px solid #fff;
        margin: 0 auto;
        text-align: center;
      }
      .next{
        width: 20%;
        font-size: 20px;
        color: #fff;
        margin: 70px auto 0;
        text-align: center;
      }
    }
  }
</style>



