<template>
  <div id="webHeader">
    <img src="../../static/images/logo.png" alt="">
    <div class="item">
      <router-link to="/">首页</router-link>
      <router-link to="/example">示例</router-link>
      <router-link to="/extend">扩展</router-link>
      <router-link to="/template">模板</router-link>
      <router-link to="/team">团队介绍</router-link>
      <a href="#"><i class="iconfont icon-GitHub"></i>GitHub</a>
    </div>
  </div>
</template>

<script>
    export default {
        name: "PublicHeader"
    }
</script>

<style scoped lang="scss">
  #webHeader{
    height: 65px;
    padding-right: 30px;
    border-bottom: 2px solid transparent;
    background-color: rgba(135,206,235,.05);
    /*logo*/
    img{
      height: 100%;
      display: block;
      float: left;
    }
    /*menu*/
    .item{
      float: right;
        a{
          color:#34495e;
          font-size: 16px;
          line-height: 65px;
          display: block;
          box-sizing: border-box;
          border-bottom:2px solid transparent;
          float: left;
          padding:0 15px;
          margin: 0 5px;
          &:hover{
            border-bottom:2px solid #16c9e9;
            color:#16c9e9;
          }
        }
      .router-link-exact-active{
        border-bottom:2px solid #16c9e9;
        color:#16c9e9;
      }
    }
  }
</style>



