<template>
    <div id="templateClassify">

       <ul class="classify">
              <p>模板分类</p>
              <li>
                <a href="https://vbeeteam.github.io/Vbeeback-wph/#/">后台管理</a>
              </li>
              <li>
                <a href="https://aalang.github.io/www.ios/#/">商城购物</a>
              </li>
              <li>
                <a href="#">主页博客</a>
              </li>
              <li>
                <a href="#">企业门户</a>
              </li>
              <li>
                <a href="#">其他模板</a>
              </li>
            </ul>
          <div class="bg">
            <webz-slider :img="picc"></webz-slider>
          </div>
    </div>
</template>

<script>
    import WebzSlider from "../webZuja/WebzSlider";
    export default {
        name: "TemplateClassify",
      components: {WebzSlider},
      data(){
          return{
            picc:[
              {pic:"./static/images/banner.png"},
              {pic:"./static/images/banner2.png"},
              {pic:"./static/images/banner1.png"}
              ]
          }
      }
    }
</script>

<style scoped lang="scss">
  #templateClassify{
    width: 100%;
    height: 450px;
    margin:100px auto;
    padding-left:20px;
    position: relative;
    box-sizing: border-box;
    position: relative;
    .bg{
      position: absolute;
      left:0;
      top:0;
      width:100%;
      height:100%;
    }
    .classify{
      position: absolute;
      left:30px;
      top:-70px;
      width: 16%;
      height: 520px;
      background: #fff;
      z-index: 999;
      p{
        font-size: 18px;
        width: 100%;
        height: 70px;
        background: #16c9e9;
        color:#fff;
        text-align: center;
        line-height: 70px;
      }
      li{
        font-size: 16px;
        width: 100%;
        height: 76px;
        text-align: center;
        line-height: 76px;
        border-bottom: 1px solid #f0f0f0;
        a{
          display: block;
          width: 100%;
          height: 100%;
          color:#333333;
          &:hover{
            background: #f0f0f0;
            color:#16c9e9;
          }
        }
      }
    }
  }
</style>



