<template>
  <div class="news">
    <div class="news-1">
      <p>日历</p>
      <div>
        &lt;template&gt;<br/>
        &lt;div id="dateContainer"&gt;<br/>
        &lt;div class="date__showNowTime"&gt;<br/>
        今天是 &lt;span v-text="ynow"&gt;&lt;/span&gt;年&lt;span v-text="mnow+1"&gt;&lt;/span&gt;月&lt;span v-text="dnow"&gt;&lt;/span&gt;日<br/>
        &lt;/div&gt;<br/>
        &lt;div class="nowTime"&gt;<br/>
        &lt;button class="preMon" @click="preMon"&gt;上个月&lt;/button&gt;<br/>
        &lt;div class="thisMon"&gt;<br/>
        &lt;span v-text="ynow"&gt;&lt;/span&gt;/<br/>
        &lt;span v-text="mnow+1"&gt;&lt;/span&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;button class="nextMon" @click="nextMon"&gt;下个月&lt;/button&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;table border="1" id="table"&gt;<br/>

        &lt;/table&gt;<br/>

        &lt;/div&gt;<br/>
        &lt;/template&gt;<br/>

      </div>
    </div>
    <div class="news-2">
        <div class="news-3">
          <webz-news></webz-news>
        </div>
    </div>
  </div>
</template>

<script>
    import WebzNews from "../webZuja/WebzNews";
    export default {
        name: "WebRightNews",
      components: {WebzNews},
    }
</script>

<style lang="scss">
  .news{
    width:100%;
    height:100%;
    .news-1{
      width:50%;
      height:100%;
      float: left;
      padding-left: 15px;
      padding-top: 35px;
      background-color: rgba(135,206,235,.8);
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar{
        width: 0;
      }
      p{
        font-size: 16px;
        font-weight: 600;
      }
      div{
        font-size: 14px;
        padding: 10px 15px;
      }
    }
    .news-2{
      width:50%;
      height:100%;
      float: left;
      .news-3{
        width:80%;
        height:100%;
        margin:50px auto;
      }
    }
  }
</style>



