<template>
<div class="tck">
  <div class="tck-1">
    <p>弹出框：</p>
    <div>
      <p>父组件</p>
      <div>
        &lt;div&gt;<br>
        &lt;p class="onep"&gt;<br/>
        &lt;button @click="getChildrenFunction"&gt;<br/>
        信息<br/>
        &lt;/button&gt;<br/>
        &lt;button class="buttontwo" @click="getChildrenFunctiontwo"&gt;<br/>
        错误<br/>
        &lt;/button&gt;<br/>
        &lt;button class="buttonthere" @click="getChildrenFunctionthere"&gt;<br/>
        成功<br/>
        &lt;/button&gt;
        &lt;button class="buttonfour" @click="getChildrenFunctionfour"&gt;<br/>
        警告<br/>
        &lt;/button&gt;<br/>
        &lt;!--&lt;test-panel ref="panel"&gt;&lt;/test-panel&gt;--&gt;<br/>
        &lt;test-toast ref="toast"&gt;&lt;/test-toast&gt;<br/>
        &lt;/p&gt;<br/>
        &lt;/div&gt;<br/>
      </div>
      <p>子组件</p>
      <div>
        &lt;div&gt;<br/>
        &lt;div class="toast"  ref='toastPosition' :class="{active: toastHidden}"&gt;<br/>
        &lt;div class="toast-warpper"&gt;<br/>
        {text}<br/>
        &lt;/div&gt;<br/>
        &lt;/div&gt;<br/>
        &lt;/div&gt;<br/>
      </div>
    </div>
  </div>
  <div class="tck-2">
    <p class="onep">
      <button @click="getChildrenFunction">
        信息
      </button>
      <button class="buttontwo" @click="getChildrenFunctiontwo">
        错误
      </button>
      <button class="buttonthere" @click="getChildrenFunctionthere">
        成功
      </button>
      <button class="buttonfour" @click="getChildrenFunctionfour">
        警告
      </button>
      <!--<test-panel ref="panel"></test-panel>-->
      <test-toast ref="toast"></test-toast>

    </p>
  </div>

</div>

</template>

<script>
    export default {
        name: "Webrighttanchu",
      methods:{
        getChildrenFunction(){
          this.$nextTick(()=>{
            this.$refs.toast.toastPlugin("这是一个弹出提示框",2500)
          });
        },
        getChildrenFunctiontwo(){
          this.$nextTick(()=>{
            this.$refs.toast.toastPlugin("这是一个错误提示框",2500)
          });
        },
        getChildrenFunctionthere(){
          this.$nextTick(()=>{
            this.$refs.toast.toastPlugin("这是一个成功提示框",2500)
          });
        },
        getChildrenFunctionfour(){
          this.$nextTick(()=>{
            this.$refs.toast.toastPlugin("这是一个警告提示框",2500)
          });
        }
      }
    }
</script>

<style lang="scss" scoped>
  .tck{
    width:100%;
    height:100%;
    .tck-1{
      width:50%;
      height:100%;
      float: left;
      padding-left: 15px;
      padding-top: 35px;
      background-color: rgba(135,206,235,.8);
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar{
        width: 0;
      }
      p{
        font-size: 16px;
        font-weight: 600;
      }
      div{
        p{
          font-size: 14px;
        }
        font-size: 14px;
        padding: 10px 15px;
      }
    }
    .tck-2{
      width:50%;
      height:100%;
      float: left;
      padding-left: 15px;
      padding-top: 35px;
      box-sizing: border-box;
    }
    button{
      width: 150px;
      height: 50px;
      outline: none;
      background:#2db7f5;
      border: none;
      border-radius: 5px;
    }
    .buttontwo{
      background:#ed4014;
    }
    .buttonthere{
      background:#19be6b;
    }
    .buttonfour{
      background:#FF9900;
    }
    .onep{
      width: 500px;
      height: 1000px;
      /*float: right;*/
    }
  }

</style>



