<template>
    <div class="sanj">
      <div class="sanj-1">
        <p>省市联动：</p>
        <div>
          &lt;div id="example"&gt;<br/>
          &lt;select v-model="prov"&gt;<br/>
          &lt;option v-for="option in arr" :value="option.name"&gt;<br/>
          {&nbsp;{ option.name }&nbsp;}<br/>
          &lt;/option&gt;<br/>
          &lt;/select&gt;<br/>
          &lt;select v-model="city"&gt;<br/>
          &lt;option v-for="option in cityArr" :value="option.name"&gt;<br/>
          {&nbsp;{option.name }&nbsp;}<br/>
          &lt;/option&gt;<br/>
          &lt;/select&gt;<br/>
          &lt;select v-model="district" v-if="district"&gt;<br/>
          &lt;option v-for="option in districtArr" :value="option.name"&gt;<br/>
          {&nbsp;{option.name }&nbsp;}<br/>
          &lt;/option&gt;<br/>
          &lt;/select&gt;<br/>
          &lt;/div&gt;<br/><br><br>
          methods: {<br>
          updateCity: function () {<br>
          for (var i in this.arr) {<br>
          var obj = this.arr[i];<br>
          if (obj.name == this.prov) {<br>
          this.cityArr = obj.sub;<br>
          break;<br>
          }<br>
          }<br>
          this.city = this.cityArr[1].name;<br>
          },<br>
          updateDistrict: function () {<br>
          for (var i in this.cityArr) {<br>
          var obj = this.cityArr[i];<br>
          if (obj.name == this.city) {<br>
          this.districtArr = obj.sub;<br>
          break;<br>
          }<br>
          }<br>
          if(this.districtArr && this.districtArr.length > 0 && this.districtArr[1].name) {<br>
          this.district = this.districtArr[1].name;<br>
          } else {<br>
          this.district = '';<br>
          }<br>
          }<br>
          },<br>
          beforeMount: function () {<br>
          this.updateCity();<br>
          this.updateDistrict();<br>
          },<br>
          watch: {<br>
          prov: function () {<br>
          this.updateCity();<br>
          this.updateDistrict();<br>
          },<br>
          city: function () {<br>
          this.updateDistrict();<br>
          }<br>
          }<br>

        </div>
      </div>
      <div class="sanj-2">
        <webz-sanji></webz-sanji>
      </div>
    </div>
</template>

<script>
    import WebzSanji from "../webZuja/WebzSanji";
    export default {
        name: "WebRightsanj",
      components: {WebzSanji}
    }
</script>

<style lang="scss" scoped>
  .sanj{
    width:100%;
    height:100%;
    .sanj-1{
      width:50%;
      height: 100%;
      float: left;
      padding-left: 15px;
      padding-top: 35px;
      background-color: rgba(135,206,235,.8);
      box-sizing: border-box;
      overflow-x: hidden;
      overflow-y: auto;
      &::-webkit-scrollbar{
        width: 0;
      }
      p{
        font-size: 16px;
        font-weight: 600;
      }
      div{
        font-size: 14px;
        padding: 10px 15px;
      }
    }
    .sanj-2{
      width:50%;
      height: 100%;
      float: left;
      box-sizing: border-box;
      padding-top: 35px;
      padding-left: 15px;
    }
  }
</style>



