<template>
    <div id="templatenews">
      <div class="newsTitle">
        <p>热门模板</p>
      </div>
      <ul class="newsCon">
        <li>
          <img src="../../../static/images/login.png" alt="">
          <a href="#">登录模板</a>
        </li>
        <li>
          <img src="../../../static/images/index.jpg" alt="">
          <a href="#">商城首页模板</a>
        </li>
        <li>
          <img src="../../../static/images/imooc.png" alt="">
          <a href="#">教育模板</a>
        </li>
        <li>
          <img src="../../../static/images/tb.png" alt="">
          <a href="#">商城首页模板</a>
        </li>
      </ul>
    </div>
</template>

<script>
    export default {
        name: "TemplateNews"
    }
</script>

<style scoped lang="scss">
  #templatenews{
    padding:0 30px;
      .newsTitle{
        width: 300px;
        height: 3px;
        background: #333333;
        position: relative;
        margin:0 auto 100px;
        p{
          position: absolute;
          top: -15px;
          left: 60px;
          width: 180px;
          height: 30px;
          line-height: 30px;
          text-align: center;
          color:#333333;
          font-size: 30px;
          background: #fff;
        }
      }
    /*热门模板*/
    .newsCon{
      li{
        width: 18%;
        border: 1px solid #ccc;
        margin-right: 50px;
        float: left;
        margin-bottom: 50px;
        padding:20px;

        img{
          width: 200px;
          height: 350px;
          display: block;
          margin:0 auto;

        }
        a{
          width: 100%;
          font-size: 14px;
          color:#333333;
          display: block;
          text-align: center;
          line-height: 50px;
          &:hover{
            text-decoration: underline;
            color:#16c9e9;
          }
        }
      }
      li:last-of-type{
        margin-right:0;
      }
    }
  }
</style>



