import Vue from 'vue'
import Router from 'vue-router'
import WebFile from '@/pages/WebFile'
import WebExample from '@/pages/WebExample'
import WebTeam from '@/pages/WebTeam'
import WebIndex from  '@/pages/WebIndex'
import WebTemplate from  '@/pages/WebTemplate'
import WebExtend from  '@/pages/WebExtend'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'WebIndex',
      component: WebIndex
    },
    {
      path: '/file',
      name: 'WebFile',
      component: WebFile
    },
    {
      path: '/example',
      name: 'WebExample',
      component: WebExample
    },
    {
      path: '/team',
      name: 'WebTeam',
      component: WebTeam
    },
    {
      path: '/template',
      name: 'WebTemplate',
      component: WebTemplate
    },
    {
      path: '/extend',
      name: 'WebExtend',
      component: WebExtend
    },
  ]
})



