<template>
    <div class="from">
      <div class="from-1">
        <p>数据表格</p>
        <div>
          &lt;template&gt;<br/>
          &lt;div id="app1"&gt;<br/>
          &lt;div class="panel panel-primary"&gt;<br/>
          &lt;div class="panel-heading"&gt;用户管理&lt;/div&gt;<br/>
          &lt;table class="table table-bordered table-striped text-center"&gt;<br/>
          &lt;thead&gt;<br/>
          &lt;tr&gt;<br/>
          &lt;th&gt;序号&lt;/th&gt;<br/>
          &lt;th&gt;用户名&lt;/th&gt;<br/>
          &lt;th&gt;年龄&lt;/th&gt;<br/>
          &lt;th&gt;毕业学校&lt;/th&gt;<br/>
          &lt;th&gt;操作&lt;/th&gt;<br/>
          &lt;/tr&gt;<br/>
          &lt;/thead&gt;<br/>
          &lt;tbody&gt;<br/>
          &lt;tr v-for ="(user,index) in users"&gt;<br/>
          &lt;td&gt;{&nbsp;{index+1}&nbsp;}&lt;/td&gt;<br/>
          &lt;td&gt;{&nbsp;{user.name}&nbsp;}&lt;/td&gt;<br/>
          &lt;td&gt;{&nbsp;{user.age}&nbsp;}&lt;/td&gt;<br/>
          &lt;td&gt;{&nbsp;{user.school}&nbsp;}&lt;/td&gt;<br/>
          &lt;td&gt;&lt;button v-on:click="remove(index)"&gt;remove&lt;/button&gt;&lt;/td&gt;<br/>
          &lt;/tr&gt;<br/>
          &lt;tr&gt;<br/>
          &lt;td&gt;&lt;/td&gt;<br/>
          &lt;td&gt;&lt;input type="text"  id="name" v-model="user.name"/&gt;&lt;/td&gt;<br/>
          &lt;td&gt;&lt;input type="text" id="age" v-model="user.age"/&gt;&lt;/td&gt;<br/>
          &lt;td&gt;&lt;input type="text" id="school" v-model="user.school"/&gt;&lt;/td&gt;<br/>
          &lt;td&gt;&lt;button @click="insert"&gt;insert&lt;/button&gt;&lt;/td&gt;<br/>
          &lt;/tr&gt;<br/>
          &lt;/tbody&gt;<br/>
          &lt;/table&gt;<br/>
          &lt;/div&gt;<br/>
          &lt;/div&gt;<br/>
          &lt;/template&gt;<br/>

        </div>
      </div>
      <div class="from-2">
        <webz-from></webz-from>
      </div>
    </div>
</template>

<script>
    import WebzFrom from "../webZuja/WebzFrom";
    export default {
        name: "WebRightFrom",
      components: {WebzFrom}
    }
</script>

<style lang="scss" scoped>
.from{
  width:100%;
  height:100%;
  .from-1{
    width:50%;
    height:100%;
    float: left;
    padding-left: 15px;
    padding-top: 35px;
    background-color: rgba(135,206,235,.8);
    box-sizing: border-box;
    overflow-x: hidden;
    overflow-y: auto;
    &::-webkit-scrollbar{
      width: 0;
    }
    p{
      font-size: 16px;
      font-weight: 600;
    }
    div{
      font-size: 14px;
      padding: 10px 15px;
    }
  }
  .from-2{
    width:50%;
    height:100%;
    float: left;
  }
}
</style>



