<template>
    <div class="mask">
        <div class="mask-1">
          <p>遮罩层：</p>
          <div>
            &lt;template&gt;<br/>
            &lt;div id="app1"&gt;<br/>
            &lt;a href="javascript:0" @click="showChose=!showChose"&gt;点击显示遮罩层&lt;/a&gt;<br/>
            &lt;transition name="mybox"&gt;<br/>
            &lt;div class="zhezh" v-show="showChose"&gt;<br/>
            &lt;/div&gt;<br/>
            &lt;/transition&gt;<br/>
            &lt;transition name="fade"&gt;<br/>
            &lt;div class="hides" v-show="showChose"&gt;<br/>
            &lt;a @click="showChose=false" href="javascript:0"&gt;关闭遮罩层&lt;/a&gt;<br/>
            &lt;/div&gt;<br/>
            &lt;/transition&gt;<br/>
            &lt;/div&gt;<br/>
            &lt;/template&gt;<br/>
          </div>
          <div>
            .fade-enter-active, .fade-leave-active {<br/>
            transition: all 1s;<br/>
            margin-bottom: 0rem;<br/>
            }<br/>
            .fade-enter, .fade-leave-active {<br/>
            margin-bottom: -6rem;<br/>
            }<br/>
            .mybox-enter-active,.mybox-leave-active{<br/>
            transition: all 1s;<br/>
            opacity: 1;<br/>
            }<br/>
            .mybox-enter,.mybox-leave-active{<br/>
            opacity: 0;<br/>
            }<br/>
          </div>
        </div>
        <div class="mask-2">
            <div class="mask-3">
              <img src="../../../static/images/iphone2.png" alt="">
              <div class="mask-4">
                <webz-mask></webz-mask>
              </div>
            </div>
        </div>
    </div>
</template>

<script>
    import WebzMask from "../webZuja/WebzMask";
    export default {
        name: "WebRightmask",
      components: {WebzMask}
    }
</script>

<style lang="scss" scoped>
  .mask{
    width:100%;
    height:100%;
    box-sizing: border-box;
      .mask-1{
        width:50%;
        height:100%;
        float: left;
        padding-left: 15px;
        padding-top: 35px;
        box-sizing: border-box;
        overflow-x: hidden;
        overflow-y: auto;
        background-color: rgba(135,206,235,.6);
        &::-webkit-scrollbar{
          width: 0;
        }
        p{
          font-size: 16px;
          font-weight: 600;
        }
        div{
          font-size: 14px;
          padding: 10px 15px;
        }
      }
    .mask-2{
      padding-top: 35px;
      width:50%;
      height:100%;
      float: left;
      .mask-3{
        width:50%;
        height:90%;
        margin:0 auto;
        position: relative;
        img{
          width:100%;
          height:100%;
        }
        .mask-4{
          width:241px;
          height:380px;
          position: absolute;
          left:25px;
          top:60px;
          background:#fff;
          overflow: hidden;
          margin-top: -4px;
        }
      }
    }
  }
</style>



