<template>
  <div class="slider" ref="slider">
    <div ref="pro" class="process" :style="{width}"> </div>
    <div class="thunk" ref="thunk" :style="{left}">
      <div @mousedown="show=true" @mouseup="show=false" @mouseleave="show=false" class="block"></div>
      <div v-show="show" class="tips">
        <span>{{scale*100}}</span>
        <i class="fas fa-caret-down"></i>
      </div>
    </div>
  </div>
</template>

<script>
    export default {
        name: "WebzSlid1",
      props:["min","max","value","color"],
      data(){
        return {
          slider:null,
          thunk:null,
          per:this.value,
          show:false
        }
      },
      mounted(){
        // 通过ref 更改 background color.
        this.$refs.pro.style.backgroundColor = this.color;
        this.slider = this.$refs.slider;
        this.thunk = this.$refs.thunk;
        var _this = this;
        this.thunk.onmousedown = function(e){
          var width = parseInt(_this.width);
          var disX = e.clientX;
          document.onmousemove = function(e){
            // 当value改变时会通过计算属性计算left，width的值
            var newWidth = e.clientX - disX + width;

            //鼠标移动时重新计算百分比
            var scale = newWidth /  _this.slider.offsetWidth;
            _this.per = Math.ceil((_this.max - _this.min) * scale + _this.min);
            _this.per = Math.max(_this.per,_this.min);
            _this.per = Math.min(_this.per,_this.max);
          }
          document.onmouseup = function(){
            // 鼠标松开时清空
            document.onmousemove = document.onmouseup =null;
          }
          return false;
        }
      },
      computed:{
        scale(){
          return (this.per - this.min) / (this.max-this.min);
        },
        width(){
          if(this.slider){
            return this.slider.offsetWidth * this.scale + 'px';
          }else{
            return 0 + 'px'
          }
        },
        left(){
          if(this.slider){
            return this.slider.offsetWidth * this.scale - this.thunk.offsetWidth/2 + 'px';
          }else{
            return 0 + 'px'
          }
        }
      },
    }
</script>

<style lang="scss" scoped>
  .box{margin:100px auto 0;width:80%}
  .clear:after{
    content:'';
    display:block;
    clear:both
  }
  .slider{
    position:relative;
    margin:20px 0 50px;
    width:400px;
    height:5px;
    background:#e4e7ed;
    border-radius:5px;
    cursor:pointer
  }
  .slider .process{
    position:absolute;
    left:0;
    top:0;
    width:112px;
    height:5px;
    border-radius:5px;
    background:red;
  }
  .slider .thunk{
    position:absolute;
    left:100px;
    top:-5px;
    width:20px;
    height:20px
  }
  .slider .block{
    width:10px;
    height:10px;
    border-radius:50%;
    border:2px solid #409eff;
    background:rgba(255,255,255,1);
    transition:.2s all
  }
  .slider .tips{
    position:absolute;
    left:-7px;
    bottom:30px;
    min-width:15px;
    text-align:center;
    padding:4px 8px;
    background:#000;
    border-radius:5px;
    height:24px;
    color:#fff
  }
  .slider .tips i{
    position:absolute;
    margin-left:-5px;
    left:50%;
    bottom:-9px;
    font-size:16px;
    color:#000}
  .slider .block:hover{
    transform:scale(1.1);
    opacity:.6}
</style>



